
// Nav container
.nav-bar {
  width     : 100%;
  position  : fixed;
  top       : 0;
  left      : 0;
  z-index   : 1000;
  padding   : 0.5rem 2rem;
  background-color: $dark-color;
}

// Desktop Navigation Styles
.nav-logo img {
  width: 80px;
  height: auto
}

.desktop-nav {
  padding: 0 4rem;
  .menu-social a {
    display   : inline-block;
    padding   : 0.5rem;
    margin    : 0.5rem auto;
    svg {
      width   : 20px;
      fill    : white;
      &:hover {
        fill: $accent-color;
        @include transition;
      }
    }
  }
  .flex-center {
    flex-direction: row;
  }
  .nav-title {
    font-family: $heading-font-family;
    text-transform: uppercase;
    font-weight: 300;
    font-size: 1.2rem;
    color: $light-color;
    // margin: auto;
    margin-left: 1rem;
  }
}

nav a,
nav span {
  font-size      : 1rem;
  font-family    : $body-font-family;
  letter-spacing : 2px;
  text-transform : uppercase;
  padding        : 0.25rem;
  margin-right   : 2.5rem;
  color          : $light-color;
  @include transition;
  &:last-of-type {
    margin       : 0;
  }
  &:hover {
    color: $accent-color;
    // text-decoration: underline;
    @include transition;
  }
}

// Mobile Navigation
.mobile-nav {
  width    : 100%;
  display  : none;
  .nav-title {
    text-transform: uppercase;
  }
}

.burger-container {
  margin-left       : auto;
  position          : relative;
  z-index           : 1000;
  cursor            : pointer;
}

.burger {
  @include            transition;
  position          : absolute;
  top               : -23px;
  right             : 0;
  width             : 50px;
  height            : 45px;
  z-index           : 1000;
  cursor            : pointer;
}


.burger span {
  @include            transition;
  display           : block;
  position          : absolute;
  left              : 25%;
  width             : 1.5rem;
  height            : 0.1rem;
  opacity           : 1;
  border-radius     : 4px;
  background        : white;
}

.burger-container,
.burger span,
.burger {
  -moz-transform    : rotate(0deg);
  -o-transform      : rotate(0deg);
  -webkit-transform : rotate(0deg);
  transform         : rotate(0deg);
}

.burger span:nth-child(1) {
  top : 0px;
}

.burger span:nth-child(2),
.burger span:nth-child(3) {
  top : 7px;
}

.burger span:nth-child(4) {
  top : 14px;
}

.burger.open span:nth-child(1) {
  top   : 7px;
  left  : 50%;
  width : 0%;
}

.burger.open span:nth-child(2) {
  -moz-transform    : rotate(45deg);
  -o-transform      : rotate(45deg);
  -webkit-transform : rotate(45deg);
  transform         : rotate(45deg);
}

.burger.open span:nth-child(3) {
  -moz-transform    : rotate(-45deg);
  -o-transform      : rotate(-45deg);
  -webkit-transform : rotate(-45deg);
  transform         : rotate(-45deg);
}

.burger.open span:nth-child(4) {
  top   : 7px;
  left  : 50%;
  width : 0%;
}

