.mobile-menu {
  z-index          : 999;
  position         : absolute;
  top              : 0;
  left             : 0;
  width            : 100%;
  height           : auto;
  padding          : 1rem;
  background-color : rgba(93,93,93,.95);
  overflow-y       : scroll;
  li {
    border-bottom  : 1px solid #cecece;
    display        : block;
    padding        : 1rem;
    padding-right  : 0;
    a {
      text-transform   : uppercase;
      color            : white;
      letter-spacing   : 2px;
      font-size        : 0.8rem;
    }
  }
  .menu-social a {
    display   : inline-block;
    padding   : 0.5rem;
    margin    : 0.5rem auto;
    svg {
      width   : 20px;
      fill    : white
    }
  }
}
