@import '_resets';

// Sass variables
// Sass global variables must come BEFORE partials that use them and declared on the 'main.scss' file
$dark-color                      : #231f20;
$light-color                     : #eaeaea;
$accent-shade                    : #8e793e;
$accent-color                    : #ad974f;
$body-font-family                : 'Quicksand', sans-serif;;
$heading-font-family             : 'Josefin Sans', sans-serif;;
$heading-default-bottom-margin   : 2rem;
$gallery-padding                 : 1.5rem;

// Sass partials
// Add necessary partials from here onwards
@import
  '_mixins',
  '_utility',
  '_universal',
  '_padding',
  '_containers',
  '_buttons',
  '_nav',
  '_header',
  '_footer',
  '_blocks',
  '_highlight-boxes',
  '_2-image-grid',
  '_column-section-1',
  '_team-listing',
  '_cards',
  '_media-queries';

